<script>
import vueTypes from 'vue-types'
export default {
  name: 'CountryPicker',

  props: {
    options: vueTypes.array.def([]),
    value: vueTypes.any.def(null),
  },

  computed: {
    selectedOption () {
      return this.options.find(o => o.value === this.value)
    }
  },

  mounted () {
    this.scrollToSelected()
  },

  methods: {
    emitClose () {
      this.$emit('close')
    },
    emitNewValue (value) {
      this.$emit('input', value)
    },
    scrollToSelected () {
      if (!this.selectedOption || !this.$refs || !this.$refs.counrtyBox) return

      const selectedOption = document.querySelector(`[data-option-id="${this.selectedOption.id}"]`)

      if (!selectedOption) return

      const { top } = selectedOption.getBoundingClientRect()
      this.$refs.counrtyBox.scrollTop = top
    },
  },
}
</script>

<template>
  <div class="country-picker">
    <div class="country-picker__list">
      <div class="country-picker__container">
        <span
          class="country-picker__underlay"
          @click="emitClose"
        />
        <div
          class="country-picker__box"
          ref="counrtyBox"
        >
          <div
            class="country-picker__option"
            :data-option-id="option.id"
            v-for="option in options"
            :key="option.id"
            :class="{ 'country-picker__option_selected': value === option.value }"
            @click="emitNewValue(option.value)"
          >{{ option.label }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.country-picker {
  &__list {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  &__container {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  &__underlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;
    background-color: rgba($color-pure-black, 0.5);
  }

  &__box {
    position: relative;
    z-index: 1;
    width: 60%;
    min-width: 500px;
    max-width: 1000px;
    height: 60%;
    max-height: 100%;
    margin: 0 auto;
    overflow-y: auto;
    background-color: $color-white;
    border-radius: 8px;
  }

  &__option {
    padding: 6px 0;
    font-size: 18px;
    color: $color-pure-black;
    text-shadow: none;
    cursor: pointer;
  }

  &__option_selected {
    background-color: $color-blue;
    color: $color-white;
  }

  &__option:hover {
    background-color: $color-lightest-grey;
  }

  &__option_selected:hover {
    background-color: $color-blue;
  }
}
</style>
