<script>
  import CountryPicker from './CountryPicker'
  import COUNTRIES from './countries'

  const MAX_STEP = 6
  const ACCOUNT_STEP = 1
  const NAME_STEP = 2
  const EMAIL_STEP = 5

  const SM_MESSAGE_SHOW_WELCOME = 'showWelcomePage'
  const SM_MESSAGE_IMPORT = 'importBookmarks'

  const EMAIL_REGEX = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

  export default {
    name: 'WelcomeApp',

    components: { CountryPicker },

    data () {
      return {
        step: 0,
        name: '',
        email: '',
        country: '',
        noUser: true,
        emailFieldDisabled: false,
        isCountryPickerVisible: false,
        importInProgress: null,
        importId: 0,
        maxStepNumber: MAX_STEP,
        isLocked: false,
        responder: null,
        getUser: null,
      }
    },

    computed: {
      selectedValue: {
        get () {
          return this.country
        },
        set (newValue) {
          this.country = newValue
          this.hideCountryPicker()
        },
      },
      countryOptions () {
        return COUNTRIES.map((c) => {
          return {
            id: c.code,
            value: c.code,
            label: c.name,
            locale: c.locale,
          }
        })
      },
      selectedCountry () {
        return this.countryOptions.find(c => c.value.toLowerCase() === this.country.toLowerCase())
      },
      hideNext () {
        if (this.step !== EMAIL_STEP) return false
        if (this.email && EMAIL_REGEX.test(this.email)) return false

        return true
      },
    },

    watch: {
      country: 'performCountrySaving',
      step (newStep) {
        this.hideCountryPicker()

        if (newStep === NAME_STEP && this.$refs && this.$refs.nameInput) {
          setTimeout(() => { this.$refs.nameInput.focus() }, 1000)
        }
      },
    },

    created () {
      this.getUser = new Promise((resolve, reject) => {
        this.$startme.getUser(true /* Force check */)
          .then((user) => {
            this.noUser = false

            // Already got a user.
            resolve(user)
          })
          .catch((err) => {
            this.$startme.createAnonymousUser().then(user => {
              // Force cache refresh
              this.$startme.getUser(true /* Force */);
              resolve(user);
            }).catch(reject);
          })
      })

      this.getUser.then((user) => {
        // Reflect current user in fields
        this.updateUserFields(user)
        this.initCountries(user)
      })

      chrome.runtime.onMessage.addListener((request, sender, respond) => {
        switch (request.message) {
          case SM_MESSAGE_SHOW_WELCOME:
            this.responder = respond;
            return true; // Keep response channel open
        }
      })
    },

    methods: {
      performDoneAction () {
        this.done()
      },
      performCountrySaving () {
        if (this.selectedCountry) this.saveCountry(this.selectedCountry.locale)
      },
      performImportAction () {
        chrome.permissions.contains({ permissions: ['bookmarks'] })
          .then((granted) => {
            if (granted) {
              return Promise.resolve(true)
            } else {
              return chrome.permissions.request({ permissions: ['bookmarks'] })
            }
          })
          .then((granted) => {
            if (granted) this.doImport().then(this.showNextStep)
            else this.showNextStep()
          })
          .catch(this.showNextStep)

      },
      saveEmail () {
        return new Promise((resolve, reject) => {
          this.lock()

          this.getUser.then(user => {
            // Clear validity
            if (this.$refs.emailField) {
              this.$refs.emailField.setCustomValidity('')
              this.$refs.emailField.parentNode.removeAttribute('data-error')
            }

            // See if the email needs to be saved at all
            let email = this.email.trim()
            if (!email || email == user.email) {
              this.unlock()
              return resolve()
            }

            // Update user
            this.$startme.updateUser({ email }).then(user => {
              this.updateUser(user)
              this.unlock()
              resolve()
            }).catch(errors => {
              this.unlock()
              if (errors.email) {
                // Something's wrong with the email field. Compose a message
                let message = errors.email.join("\n")

                // Set a validation pattern to the field that will invalidate the current email
                let escapedEmail = email.replace(/\W/, match => '\\' + match)
                if (this.$refs.emailField) {
                  this.$refs.emailField.setAttribute('pattern', `^(?!${escapedEmail}).+$`)
                  this.$refs.emailField.select()

                  // Tell parent element about the error
                  this.$refs.emailField.parentNode.setAttribute('data-error', message)
                }
              }
              return reject('reject from this.$startme.updateUser')
            })
          })
          .catch((err) => { })
        })
      },
      doImport () {
        if (!this.importInProgress) {
          this.importInProgress = new Promise((resolve, reject) => {
            // Request import (make sure there is a user available for this)
            this.getUser.then(() => {
              chrome.runtime.sendMessage({
                message: SM_MESSAGE_IMPORT,
                quiet: true
              })
              .then((response) => {
                this.importId = parseInt(response)
                resolve()
              })
            }).catch(reject)
          })

          return this.$startme.timeout(2000);
        }

        return this.importInProgress
      },
      saveName () {
        return new Promise(resolve => {
          this.getUser.then(user => {
            let name = this.name.trim()
            if (name && name != user.name) {
              this.$startme.updateUser({ name }).then(user => this.updateUser(user))
            }
          })
          resolve()
        })
      },
      done () {
        return new Promise((resolve, reject) => {
          this.getUser.then((user) => {
            const isNewUser = user.dummy_email && user.is_anonymous

            if (this.responder) {
              this.responder({ isNewUser })
              window.close()
            } else {
              location.href = this.$startme.apiURL('/users/new_extension_installed')
            }
            resolve()
          }).catch(reject)
        })
      },
      lock () {
        this.isLocked = true
      },
      unlock () {
        this.isLocked = false
      },
      showCountryPicker () {
        this.isCountryPickerVisible = true
      },
      hideCountryPicker () {
        this.isCountryPickerVisible = false
      },
      showNextStep () {
        if (this.step === NAME_STEP) {
          this.saveName()
          this.activateNextStep()
        } else if (this.step === EMAIL_STEP) {
          this.saveEmail().then(this.activateNextStep).catch((err) => { })
        } else {
          this.activateNextStep()
        }
      },
      activateNextStep () {
        let newStep = this.step + 1

        if (newStep === ACCOUNT_STEP && !this.noUser) newStep += 1

        this.showStepN(newStep)
      },
      showPrevStep () {
        let newStep = this.step - 1

        if (newStep === ACCOUNT_STEP && !this.noUser) newStep -= 1

        this.showStepN(newStep)
      },
      showStepN (newStep) {
        if (newStep <= MAX_STEP && newStep >= 0) this.step = newStep
      },
      goToSignIn () {
        if (this.responder) {
          this.responder({ isNewUser: false, openSignIn: true })
          window.close()
        } else {
          location.href = this.$startme.apiURL('/users/sign_in')
        }
      },
      updateUserFields (user) {
        if (user.is_anonymous && (user.raw_name || user.name) === this.$t('anonymous_user').message) this.name = ''
        else if (user.raw_name || user.name) this.name = (user.raw_name || user.name).toString().trim()

        if (!user.dummy_email) {
          this.email = user.email.toString().trim()
          this.emailFieldDisabled = true // Email address can only be edited when not set
        }
        if (user.country) this.country = user.country
      },
      initCountries (user) {
        const country = this.determineUserCountry(user)

        if (country) this.hideCountryPicker()
        else this.showCountryPicker()

        // If no country is selected yet, save it immediatly
        if (user && !user.country) this.saveCountry(country.locale)
      },
      saveCountry (locale) {
        this.lock()

        const country = this.findCountryByLocale(locale, true)

        return new Promise((resolve, reject) => {
          this.getUser.then((user) => {
            // See if the country needs to be saved at all
            if (country.code === user.country) {
              this.unlock()
              return resolve()
            }

            this.$startme.updateUser({ country: country.code })
              .then((user) => {
                this.updateUser(user)
                this.unlock()
                resolve()
              })
              .catch((errors) => {
                this.unlock()

                if (this.$refs.countryControls) this.$refs.countryControls.dataset.error = errors.country.join("\n")
              })
          });
        });
      },
      updateUser(user) {
        this.getUser = Promise.resolve(user)
        this.getUser.then(this.updateUserFields)
      },
      determineUserCountry (user) {
        const browserLocale = this.$startme.getLocale()

        // Determine the country of the user
        if (user && user.country) {
          const country = this.findCountryByCountryCode(user.country)

          // Only use it when the country is valid
          if (country && country.code) return country
        }

        return this.findCountryByLocale(browserLocale, true)
      },
      findCountryByCountryCode (countryCode) {
        const country = COUNTRIES.find(country => country.code.toLowerCase() === countryCode.toLowerCase())

        return country
      },
      findCountryByLocale (locale, fallback = false) {
        const country = COUNTRIES.find(country => country.locale.toLowerCase() === locale.toLowerCase());

        if (!country && fallback) return this.findCountryByLocale(this.$startme.USER_FALLBACK_LOCALE, false)

        return country
      },
      setCountryValue (value) {
        this.country = value
        this.hideCountryPicker()
      },
    },
  }
</script>

<template>
  <main
    class="welcome-screen"
    :step="step"
    :class="[`welcome-screen_step_${step}`, { 'welcome-screen_locked': isLocked }]"
  >
    <section class="welcome-screen__slides">
      <!-- GREETING -->
      <div
        class="welcome-screen__slide welcome-screen__slide_0"
        step="0"
      >
        <h1 class="welcome-screen__head">
          <span class="welcome-screen__head-text">{{ $t('welcome_to').message }}&nbsp;start.me!</span>
        </h1>
        <p class="welcome-screen__text">{{ $t('lets_personalize_it').message }}</p>
      </div>

      <!-- CHECK FOR ACCOUNT -->
      <div
        class="welcome-screen__slide welcome-screen__slide_1"
        step="1"
      >
        <h1 class="welcome-screen__head welcome-screen__head_spaced">{{ $t('already_have_startme_account').message }}</h1>
        <p class="welcome-screen__buttons">
          <button
            class="welcome-screen__button"
            @click.stop="goToSignIn"
          >
            <span class="welcome-screen__button-text">{{ $t('yes').message }}</span>
            <span class="welcome-screen__button-text">{{ $t('signin_with_email').message }}</span>
          </button>
          <button
            class="welcome-screen__button"
            @click.stop="showNextStep"
          >
            <span class="welcome-screen__button-text">{{ $t('no').message }}</span>
            <span class="welcome-screen__button-text">{{ $t('create_new_free_account').message }}</span>
          </button>
        </p>
      </div>

      <!-- NAME -->
      <div
        class="welcome-screen__slide welcome-screen__slide_2"
        step="2"
      >
        <h1 class="welcome-screen__head">{{ $t('what_is_your_name').message }}</h1>
        <p class="welcome-screen__form">
          <span class="welcome-screen__form-head">{{ $t('my_name_is').message }}</span>
          <input
            id="name"
            class="welcome-screen__input"
            type="text"
            name="name"
            size="12"
            autocomplete="name"
            x-autocompletetype="name"
            ref="nameInput"
            v-model="name"
            :placeholder="$t('eg_name').message"
          >
        </p>
      </div>

      <!-- COUNTRY -->
      <div
        class="welcome-screen__slide welcome-screen__slide_3"
        step="3"
      >
        <h1
          class="welcome-screen__head"
          v-if="!isCountryPickerVisible"
        >{{ $t('where_do_you_come_from').message }}</h1>
        <div
          class="welcome-screen__controls"
          ref="countryControls"
        >
          <div
            class="welcome-screen__country-picker"
            v-if="isCountryPickerVisible"
          >
            <CountryPicker
              v-model="selectedValue"
              :options="countryOptions"
              @close="hideCountryPicker"
            />
          </div>

          <button
            class="welcome-screen__button"
            @click.stop="showCountryPicker"
            v-else
          >
            <span class="welcome-screen__button-text">{{ selectedCountry ? selectedCountry.label : country }}</span>
            <span class="welcome-screen__button-text">{{ $t('click_to_choose_a_different_country').message }}</span>
          </button>
        </div>
      </div>

      <!-- IMPORT -->
      <div
        class="welcome-screen__slide welcome-screen__slide_4"
        step="4"
      >
        <h1 class="welcome-screen__head">{{ $t('import_your_bookmarks_question').message }}</h1>
        <p
          class="welcome-screen__working-status"
          v-if="importInProgress"
        >{{ $t('your_bookmarks_are_being_imported').message }}</p>

        <p
          class="welcome-screen__buttons"
          v-else
        >
          <button
            class="welcome-screen__button"
            @click.stop="performImportAction"
          >
            <span class="welcome-screen__button-text">{{ $t('yes').message }}</span>
            <span class="welcome-screen__button-text">{{ $t('please_import_them').message }}</span>
          </button>
          <button
            class="welcome-screen__button"
            @click.stop="showNextStep"
          >
            <span class="welcome-screen__button-text">{{ $t('no').message }}</span>
            <span class="welcome-screen__button-text">{{ $t('i_may_do_this_later').message }}</span>
          </button>
        </p>
      </div>

      <!-- EMAIL -->
      <div
        class="welcome-screen__slide welcome-screen__slide_5"
        step="5"
      >
        <h1 class="welcome-screen__head">{{ $t('create_account').message }}</h1>
        <p class="welcome-screen__form">
          <span class="welcome-screen__form-head">{{ $t('my_email_is').message }}</span>
          <input
            id="email"
            class="welcome-screen__input"
            required
            name="email"
            type="email"
            size="25"
            autocomplete="email"
            x-autocompletetype="email"
            ref="emailField"
            v-model="email"
            :disabled="emailFieldDisabled ? 'disabled' : false"
            :placeholder="$t('eg_email').message"
          >
        </p>
      </div>

      <!-- DONE -->
      <div
        class="welcome-screen__slide welcome-screen__slide_6"
        step="6"
      >
        <p class="welcome-screen__buttons">
          <button
            class="welcome-screen__button"
            @click.stop="performDoneAction"
          >
            <span class="welcome-screen__button-text">{{ $t('done').message }}</span>
            <span class="welcome-screen__button-text">{{ $t('take_me_to_start_page').message }}</span>
          </button>
        </p>
      </div>
    </section>

    <nav class="welcome-screen__progressbar" />

    <nav
      class="welcome-screen__steps"
      v-if="step !== 3 || !isCountryPickerVisible"
    >
      <a
        class="welcome-screen__step"
        step="2"
        @click.stop="showStepN(2)"
      >{{ $t('shake_hands').message }}</a>
      <a
        class="welcome-screen__step"
        step="4"
        @click.stop="showStepN(4)"
      >{{ $t('import_bookmarks').message }}</a>
      <a
        class="welcome-screen__step"
        step="5"
        @click.stop="showStepN(5)"
      >{{ $t('create_account').message }}</a>
    </nav>

    <nav
      class="welcome-screen__nav-buttons"
      v-if="step !== 3 || !isCountryPickerVisible"
    >
      <a
        class="welcome-screen__prev"
        title="Back"
        v-if="step > 0"
        @click.stop="showPrevStep"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="100"
          height="100"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
          class="feather feather-chevron-left"
        >
          <polyline points="15 18 9 12 15 6" />
        </svg>
      </a>
      <a
        class="welcome-screen__next"
        title="Next"
        v-if="step < maxStepNumber && !hideNext"
        @click.stop="showNextStep"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="100"
          height="100"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
          class="feather feather-chevron-right"
        >
          <polyline points="9 18 15 12 9 6" />
        </svg>
      </a>
    </nav>

    <a
      class="welcome-screen__close"
      v-if="step !== 3 || !isCountryPickerVisible"
      :title="$t('close').message"
      @click.stop="performDoneAction"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="44"
        height="44"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="3"
        stroke-linecap="round"
        stroke-linejoin="round"
        class="feather feather-x"
      >
        <line
          x1="18"
          y1="6"
          x2="6"
          y2="18"
        />
        <line
          x1="6"
          y1="6"
          x2="18"
          y2="18"
        />
      </svg>
    </a>
    <a class="welcome-screen__logo-container" href="https://start.me" target="_blank" :title="$t('app_name').message">
      <img class="welcome-screen__logo" src="images/Logo-white@2x.png">
    </a>
  </main>
</template>

<style lang="scss">
html, body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: $segoe;
  font-size: $font-size-normal;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

@mixin placeholder {
  $placeholders: ":-webkit-input" ":-moz" "-moz" "-ms-input";
  @each $placeholder in $placeholders {
    &:#{$placeholder}-placeholder {
      @content;
    }
  }
}

@mixin white-text {
  color: $color-white;
  opacity: 0.5;
  text-shadow: 0 0 3px black;
}

@mixin white-button {
  @include white-text;
  cursor: pointer;

  &:hover {
    opacity: 1;
    text-decoration: none;
  }
}

*::selection {
  background-color: rgba($color-pure-black, 0.2);
}

.welcome-screen {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  transition: background-color 1s;

  $background-color: darken($color-blue, 25%);
  &_step_0 { background-color: $background-color; }
  &_step_1 { background-color: #445; }
  &_step_2 { background-color: #454; }
  &_step_3 { background-color: #544; }
  &_step_4 { background-color: #354; }
  &_step_5 { background-color: #435; }
  &_step_6 { background-color: #435; }

  &__feather-icon {
    stroke: currentColor;
  }

  &__logo {
    position: absolute;
    left: 1em;
    top: 1em;
    max-height: 2em;
    opacity: 0.5;
  }

  &__close {
    @include white-button;

    position: absolute;
    top: 0.33em;
    right: 0.4em;
    font-size: 3em;
  }

  &__slides {
    display: flex;
    top: 0;
    left: 0;
    position: absolute;
    transition: left 1s;
  }

  &_locked &__slides {
    visibility: hidden;
  }

  &__slides > * {
    position: relative;
    display: inline-flex;
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    padding: 0 10vw 15vh 10vw;
    color: white;
    font-family: $segoe;
    font-weight: 300;
    font-size: 2vw;
    text-align: center;
    text-shadow: 0 0 3px rgba($color-black, 0.5);
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.5s;
    overflow: hidden;
  }

  &__head {
    font-size: 250%;
    font-weight: 300;
    margin: 0;
    margin-bottom: 0.25em;

    &_spaced {
      white-space: normal;
    }
  }

  &__head-logo {
    height: 0.72em;
  }

  &__head-text + &__head-logo {
    margin-left: 12px;
  }

  p {
    margin: 0;
  }

  & &__slides &__slide &__input {
    background: transparent;
    border: none;
    padding: 0 0.5em;
    outline: none;
    color: $color-white;
    text-shadow: 0 0 3px rgba($color-black, 0.5);

    @include placeholder {
      color: rgba($color-white, 0.5);
      font-style: italic;
    }

    &:invalid {
      border-color: $color-red;
    }

    &:disabled {
      opacity: 0.5;
    }
  }

  & &__slides &__slide &__input {
    border-bottom: 4px solid rgba($color-white, 0.2);
  }

  &__form-head + &__input {
    margin-left: 8px;
  }

  &__button {
    @include white-button;

    padding: 0.5em;
    background: transparent;
    border: none;
    outline: none;
    border-radius: 12px;
    box-shadow: 0 0 3px $color-white;
    opacity: 0.8;

    font-weight: 100;
    color: $color-grey;
    font-size: 80%;

    &:hover, &:focus {
      box-shadow: 0 0 10px $color-white;
      opacity: 1;
    }
  }

  &__button-text {
    display: block;
  }

  &__button-text:first-of-type {
    font-size: 225%;
    font-weight: 300;
    color: $color-white;
  }

  &__buttons {
    display: flex;
    justify-content: center;
  }

  &__button:not(:last-child) {
    margin-right: 0.5em;
  }

  &__progressbar {
    @import '../progressbar';
    @include progressbar($width: 50vw, $color: rgba($color-white, 0.5), $shadow: $color-white, $duration: 10s);

    display: none;
    margin: 40vh auto 60vh auto;
  }

  &_locked &__progressbar {
    display: block;
  }

  &__steps {
    position: absolute;
    display: flex;
    bottom: 5vh;
    left: 10vw;
    width: 80vw;
    justify-content: center;
    font-family: $segoe;
    user-select: none;

    a {
      @include white-text;

      width: 15em;
      margin: 0 3vw;
      padding-top: 1em;
      border-top: 1em solid rgba($color-black, 0.5);
      text-align: center;
      text-transform: uppercase;
      text-decoration: none;
      cursor: default;
      transition: border-top-color 0.5s;
    }
  }

  &__nav-buttons {
    a {
      @include white-button;

      position: absolute;
      top: calc(50vh - 1em);
      width: 2em;
      line-height: 2em;
      font-size: 10vh;
      text-align: center;
      transform: scaleY(1.5);
      cursor: pointer;
      user-select: none;
    }
  }

  &_locked &__nav-buttons {
    display: none;
  }

  &__prev {
    left: 0;
  }

  &__next {
    right: 0;
  }

  $step-count: 7;
  $previous-steps: ();
  @for $step from 0 to $step-count {
    &[step="#{$step}"] {
      .welcome-screen__slides {
        left: $step * -100vw;

        & > [step="#{$step}"] {
          opacity: 1;
          transition: opacity 2s; // Fade in a little slower
        }
      }

      .welcome-screen__steps {
        [step="#{$step}"] {
          opacity: 1;
          border-top-color: white;
        }

        @each $previous-step in $previous-steps {
          [step="#{$previous-step}"] {
            opacity: 1;
            cursor: pointer;
            border-top-color: rgba($color-white, 0.2);
          }
        }
      }

      $previous-steps: append($previous-steps, $step);
    }
  }

  [data-error]:before {
    display: block;
    content: attr(data-error);
    padding: 0.5em;
    color: $color-red;
    text-shadow: 0 0 3px black;
  }

  &__option {
    color: $color-middle-black;
  }
}
